body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{

  background-image: url("https://png.pngtree.com/background/20210716/original/pngtree-cartoon-flowers-natural-landscape-background-picture-image_1339178.jpg");
  background-size: cover;
}

.App{
  margin-top: 5%;
}

.quote1{
  margin-top: 5%;
}

p1{
  font-size: 30px;
  font-family: "Georgia BoldItalic";
  font-weight: bold;
}

p{
  font-family: 'Lucida Calligraphy';
  font-size: 40px;
  font-weight: bold;
}
button{
  border-radius: 20%;
  font-weight: bold;
  color: darkblue;
  background-color: rgb(239, 126, 239);
  margin: 30px;
  height: 50px;
  width: 90px;
  font-size: 20px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  
}

button:hover{
  background-color: pink;
}




